import { Component, OnInit, ViewEncapsulation, AfterViewInit, AfterViewChecked, ElementRef, ViewChild, HostBinding, Renderer2, Directive} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const NUM_REGEX=/[0-9]/;
const WORD_REGEX=/w{5,}/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./ticket.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})
export class TicketComponent implements OnInit,AfterViewInit {
	@ViewChild(SmartSearchComponent)smart: SmartSearchComponent;
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ListComponent) list: ListComponent;
	 
	listHide: boolean = true; 
	 animateList: string = 'in';
	smartHide: boolean = true;
	animateSmart: string = 'out';
	grandTotal: string;
	contractId = 0;  
	ticketForm: FormGroup;  
	ticket = new TicketData();
	public msg = []; 
	branches = []; 
	bootstrapFromValues = []; 
	ticketSummaryData = []; 
	reporttypes = [
		{key:'1', name:'Contract Details'}
		
	]; 
	 exportformats = [
		{key:'1', name:'MS Excel'},
		 {key:'12', name:'HTML'}
	];
     constructor(private http: HttpClient, private route:ActivatedRoute ,private router: Router) {
    }

	  ngOnInit() {
		this.listHide = true;
    	this.animateList='out';
		this.populateDropDownData();
		this.getTodaysDate();
//		this.populateData();
		if(this.ticketSummaryData.length > 0){
			  this.listHide = false;
		}  
		this.ticketForm = new FormGroup({	
			'branchsIds' : new FormControl(''),
			'exportformat' : new FormControl(''),
			'reporttype' : new FormControl(''),
		});
	  }
	    get branchsIds(){ return this.ticketForm.get('branchsIds')};
	    get exportformat(){ return this.ticketForm.get('exportformat')};
	    get reporttype(){ return this.ticketForm.get('reporttype')};
	 
	 
	   populateDropDownData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branches = data['populateBranch'];
			  															});
	  }
	
	
     ngAfterViewInit(){
		 
		  $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	}
	 
	 
	 onSubmit(){
		   this.listHide = false;
		  this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.ticket[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
		 this.ticket['requestType']='landing';
		    const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/ticket',
								 JSON.stringify(this.ticket),
								{ headers, withCredentials: true }).subscribe(data =>{
			   					this.ticketSummaryData = data['populateLandingDetails'];
			   					this.grandTotal = data['grandTotal'];
																	}); 
		 console.log(this.ticket);
		   
	  }
	 
	 getTodaysDate(){
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/currentdate',
							 			JSON.stringify({ requestType : 'shortDate' }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.ticket.startDate= data['date'];
				  														this.ticket.endDate= data['date'];
			  															});
	 }
	 
	  populateData(){
		  this.ticket['requestType'] = 'landing';
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/ticket',
								 JSON.stringify(this.ticket),
								{ headers, withCredentials: true }).subscribe(data =>{
			   					this.ticketSummaryData = data['populateData'];
			   					this.grandTotal = data['grandTotal'];
																	}); 
		 console.log(this.ticket);
	  }
}

export class TicketData
{
	startDate: string='';
	endDate: string='';
	branchsIds: string;
	exportformat: string;
	reporttype: string;
}	
	


